<template>
  <div class="admin-container">
    <h1>لوحة التحكم</h1>
    <ag-grid-vue
      :columnDefs="columnDefs"
      :rowData="requests"
      style="height: 500px"
      class="ag-theme-quartz"
      :enableRtl="true"
    >
    </ag-grid-vue>
  </div>
</template>

<script>
import "ag-grid-community/styles/ag-grid.css" // Mandatory CSS required by the grid
import "ag-grid-community/styles/ag-theme-quartz.css" // Optional Theme applied to the grid
import { AgGridVue } from "ag-grid-vue3" // AG Grid Component
import axios from "axios"
import qs from "qs"
import { serverUrl } from "../const"
export default {
  components: {
    AgGridVue,
  },
  data() {
    return {
      requests: [],
      columnDefs: [
        { field: "name", headerName: "الاسم", sortable: true },
        { field: "phoneNumber", headerName: "رقم الهاتف", sortable: true },
        {
          headerName: "التصرف",
          cellRenderer: (params) => {
            if (params.data?._id !== null) {
              // put the value in bold
              const div = document.createElement("div")
              div.classList.add("copy-icon-container")
              div.innerHTML = `
                  <button>التقاط صورة</button>
            `
              this.addClickEventListener(div, params.data)
              return div
            } else {
              return ""
            }
          },
          width: 150,
        },
        {
          headerName: "الصورة",
          cellRenderer: (params) => {
            if (params.data?.urlPicture) {
              // put the value in bold
              const div = document.createElement("div")
              div.classList.add("copy-icon-container")
              div.innerHTML = `
                  <a href="${serverUrl}/api/admin/picture/${params.data?._id}" target="_blank">فتح الصورة</button>`
              //this.addClickEventListener(div, params.data)
              return div
            } else {
              return ""
            }
          },
        },
      ],
    }
  },
  methods: {
    async takePhoto(data) {
      const idOrder = data?._id
      const phoneNumber = data?.phoneNumber
      try {
        // Créer un input de type "file" pour permettre à l'utilisateur de choisir une photo depuis la galerie ou de prendre une photo avec l'appareil photo du téléphone
        const inputElement = document.createElement("input")
        inputElement.type = "file"
        inputElement.accept = "image/*" // Accepter tous les types d'images
        inputElement.capture = "environment" // Utiliser la caméra arrière du téléphone si disponible

        // Ajouter un écouteur d'événement pour détecter lorsque l'utilisateur sélectionne une image
        inputElement.addEventListener("change", async (event) => {
          const file = event.target.files[0] // Récupérer le fichier sélectionné par l'utilisateur
          if (file) {
            // Convertir le fichier en base64
            const reader = new FileReader()
            reader.readAsDataURL(file)
            reader.onload = async () => {
              const imageDataUrl = reader.result // Obtenir l'URL de l'image en base64
              // Enregistrer l'image sur le serveur
              const response = await axios.post(
                `${serverUrl}/api/admin/uploadPhoto`,
                {
                  imageData: imageDataUrl,
                  orderId: idOrder,
                }
              )
              // Récupérer l'URL de l'image enregistrée sur le serveur depuis la réponse
              const imageUrl = response.data?.imageUrl
              console.log("imageUrl", imageUrl)
              await this.sendPicture(imageUrl, phoneNumber)
              // Afficher une confirmation à l'utilisateur
              alert("Photo enregistrée avec succès !")
            }
          }
        })

        // Cliquer sur le bouton d'upload de fichier
        inputElement.click()
      } catch (error) {
        console.error("Erreur lors de la prise de la photo :", error)
        // Gérer les erreurs ici
      }
    },

    addClickEventListener(element, data) {
      element.addEventListener("click", () => this.takePhoto(data))
    },
    async fetchRequests() {
      // جلب الطلبات من الخادم
      const response = await axios.get(`${serverUrl}/api/admin/requests`)
      console.log("response", response)
      this.requests = response.data
    },
    async sendPicture(url, phoneNumber) {
      console.log("url", url)
      var data = qs.stringify({
        token: "j290mey56mbdrzo3",
        to: phoneNumber,
        image: url,
        // image:
        //  "https://file-example.s3-accelerate.amazonaws.com/images/test.jpg",
        caption: "اهداء",
      })
      console.log("phoneNumber", phoneNumber)
      var config = {
        method: "post",
        url: "https://api.ultramsg.com/instance81883/messages/image",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        data: data,
      }

      axios(config)
        .then(function (response) {
          console.log(JSON.stringify(response.data))
          alert("تم التقاط الصورة وإرسالها إلى رقم الواتساب!")
        })
        .catch(function (error) {
          console.log(error)
        })
    },
  },
  mounted() {
    this.fetchRequests()
  },
}
</script>

<style scoped>
.admin-container {
  text-align: right;
  margin: 20px;
}
</style>
